function ScrollToTop() {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
}

function App() {
  let [tab, setTab] = useState(0)
  return (
    <React.Fragment>
    <Reset />
    <div className="App">
      <Header/>
      <main>
        <ScrollToTop/>
        <Routes>
          <Route path='/' element={
            <>
              {/*    */}
              <section className='sc main-bg' id='main-bg'>
                {/* <MainBg></MainBg> */}
                <div className='main-bg-desc'>
                  <h4 className='main-bg-tt'>" "</h4>
                  <img src='img/main_bg_logo.png'/>
                </div>
              </section>
              {/*   */}
              <MenuList/>
              {/*    (  ) */}
              <Brand tab={tab} setTab={setTab}/>
              {/*    */}
              <Soap/>
              {/*      About  */}
              <About/>
            </>
          }/>
          <Route path='/intro' element={<Intro/>}/>
          <Route path='/menu' element={<Menu/>}/>
          <Route path='/menu/:id' element={<Item/>}/>
          <Route path='/cart' element={<Cart/>}/>
          <Route path='/locate' element={<Locate/>}/>
        </Routes>
      </main>
      <Footer/>
    </div>
    </React.Fragment>
  );
}

