import { useDispatch, useSelector } from "react-redux"
import { useParams } from "react-router-dom"
import { addItem, plusCart, minusCart, minusItem } from "../store"
import { useEffect } from "react"
//   
const Item = () => {
    let allItems = useSelector(state => {return state.allItems})
    let {id} = useParams()
    let showItem = allItems.find(x => {return x.id == id})
    let dispatch = useDispatch()
    console.log(showItem)
    return(
        <section className="sc1320 item">
            <div className="item-container">
                <div className="item-img">
                    <img src={`/${showItem.img}`} alt={`Item ${showItem.name}`} />
                </div>
                <div className="item-contents">
                    <h4 className="item-tt">{showItem.name}</h4>
                    {/*       */}
                    {
                        showItem.contents == 'pudding' 
                        ?           
                        <p className="item-text notice-pudding">           .</p> 
                        : 
                        <p className="item-text notice-pudding">             .</p> 
                    }
                    {/*     */}
                    {
                        showItem.description != undefined ? <p className="item-text item-desc">{showItem.description}</p> : null
                    }
                    <div className="item-btn-container">
                        <div className="cnt-btn-container">
                            <button className="cnt-btn minus" onClick={()=>{ dispatch(minusItem(showItem.id));} }></button>
                            <p className="item-cnt">{showItem.count}</p>
                            <button className="cnt-btn plus" onClick={()=>{ dispatch(addItem(showItem.id));}}></button>
                            <p className="item-text item-price">{showItem.price}</p>
                        </div>
                        <div className="cart-btn-container">
                            <p className="item-all-price">{showItem.priceNum * showItem.count}</p>
                            <button className="cart-btn" onClick={()=>{ dispatch(plusCart(showItem))}}></button>
                        </div>
                    </div>
                    
                </div>
            </div>
            
        </section>
    )
}

export default Item